<template>
  <div>
    <h1>SC Calculator V0.0.2</h1>
    <van-form @submit="submit">
          <div>
            格式：物质名称 物质的量浓度 （科学计数法）<br/>
例如：HS- 9.5 -5
          <van-divider>输入已存在物质</van-divider>
      <van-form @submit="addExistingIons">
        <van-row>
          <van-col span="6">
            <van-field type="text" name="ionNam" v-model="ionName" placeholder="HS-"></van-field>
          </van-col>
          <van-col span="4">
            <van-field type="number" name="ionNum" v-model="ionNumber" placeholder="9.5"></van-field>
          </van-col>
          <van-col span="6"><p>*10^</p>
          </van-col>
          <van-col span="8">
            <van-stepper type="number" name="ionExp" v-model="ExpStepper" min="-14" max="0" integer />
          </van-col>
        </van-row>
            <van-button round type="primary" native-type="submit">添加</van-button>
      </van-form>
      <ul>
        <li v-for="i in cItem" :key="i.ionNam">{{i.ionNam}}: {{i.ionNum}}<van-button size="mini" plain type="warning" @click="removeIon(i.ionNam)">删除</van-button></li>
      </ul>
              <van-notice-bar color="#1989fa" background="#ecf9ff"
  scrollable
  mode="closeable"
  text="如果溶液中已经有某些物质了，请在这里添加。H+和OH-不算在内。
          如果没有的话可以留空。填写完点击绿色按钮添加。写错可以点击右侧的删除按钮移除。
          "/>
              <van-divider>填写pH和加入的物质</van-divider>
      <van-field v-model="ipim" name="ipim" label="加入物质名称" placeholder="加入物质名称" :rules="[{ required: true, message: '请填写加入物质名称' }]" />
      <van-field v-model="pH" type="number" name="pH" label="pH" placeholder="pH" :rules="[{ required: true, message: '请填写pH' }]" />
      <van-notice-bar color="#1989fa" background="#ecf9ff"
 scrollable
  mode="closeable"
  text="请在这里填写溶液初始的pH数值以及加入的物质（不能留空）"/>
    </div>
              <van-divider>提交参数</van-divider>
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
    <van-divider>运算结果</van-divider>
    <!-- <van-stepper theme="round" v-model="GrandStepper" min="0" integer/>
    <van-form>
      <van-row  v-for="n in GrandStepper" :key="n">
      <van-col span="6"><van-field placeholder="HS-"></van-field></van-col>
      <van-col span="4"><van-field placeholder="9.5"></van-field></van-col>
