    <p v-if="input_item != '' ">出错的input:{{ input_item }}产生了-log({{ cH }})</p>
    <br />
    <p>{{ result }}</p>
              <van-divider>目前支持的物质</van-divider>
    <p>'HCl', <br />'H2SO4', <br />'KOH', <br />'BaOH2', <br />'H2S': 7.05, <br />'HAc': 4.75, <br />'H2SO3(1)': 1.81, <br />'H3PO4(1)': 2.12, <br />'HCOOH': 3.75, <br />'Cu(OH)2' <br /></p>
    <p>目前的问题:相对来说比较容易出现负根, 需要改进估算(目前是经过统计学改良(乱猜)的估算)方法; ...</p>
  </div>
</template>

<script>
import {
  Form,
  Button,
  Field,
  Stepper
} from 'vant'
export default {
  name: 'Cal',
  components: {
    [Form.name]: Form,
    [Button.name]: Button,
    [Field.name]: Field,
    [Stepper.name]: Stepper
  },
  data() {
    return {
      ipim: '',
      pH: '',
      result: '',
      input_item: '',
      cItem: [],
      GrandStepper: 0,
      ExpStepper: -1,
      ionName: '',
      ionNumber: 0,
      ionExponent: 0
    }
  },
  methods: {
    submit(e) {
      var that = this
      that.input_item = ''
      that.result = ''
      var data = {
        ipim: that.ipim,
        pH: that.pH,
        cItem: that.cItem
      }
      console.log('submit', data)
      that.$http.post(that.$api + 'phcal', data).then(
        (res) => {
          console.log('res', res.data)
          if (res.data.error === 0) {
            that.result = res.data.result
          } else {
            that.cH = res.data.result
            that.input_item = {
              ipim: res.data.ipim,
              pH: res.data.pH
            }
          }
        }
      )
    },
    addExistingIons(values) {
      console.log('adding existing ions', values, this.ExpStepper)
      if (values) {
        var number = values.ionNum
        values.ionNum = parseFloat(number) * (10 ** parseFloat(this.ExpStepper))
        console.log(values)
        var idx = this.cItem.findIndex(obj => obj.ionNam === values.ionNam)
        if (idx === -1) {
          this.cItem.push(values)
        }
        console.log(this.cItem)
      }
    },
    removeIon(iName) {
      var idx = this.cItem.findIndex(obj => obj.ionNam === iName)
      this.cItem.splice(idx, 1)
    }
  }
}
</script>
